<template>
  <div class="container">
    <div class="form">
      <real-query-form :realForm="realForm" ref="realQueryForm"></real-query-form>
    </div>
    <div class="option">
      <el-button  type="primary" size="small" plain @click="inspectTable">检视</el-button>
      <el-button  type="primary" size="small" plain  @click="exportTable">导出报表</el-button>
    </div>
    <div class="data">
      <div class="table">
        <base-table
          :columns="columns"
          :data="orgRealResultData"
          row-key="id">
        </base-table>
      </div>
    </div>
  </div>
</template>

