        {prop: 'premiumPercent', width: 120, label: '保费占比(%)', align: 'center'},
        {prop: 'finalPayRate', width: 120, label: '合并终极赔付率(%)', align: 'center'},
        {prop: 'fullPayRate', width: 200, label: '满期赔付率(含IBNR)(%)', align: 'center'},
        {prop: 'marketRate', width: 120, label: '市场费用率(%)', align: 'center'},
        {prop: 'operationCostRate', width: 120, label: '经营成本率(%)', align: 'center'}
      ],
      orgRealResultData: [
        {
          id: '1',
          org: '太原支公司',
          premiumIncome: '4942.15',
          premiumPercent: '15.38',
          finalPayRate: '56.86',
          fullPayRate: '57.96',
          marketRate: '17.5',
          operationCostRate: '98.2'
        },
        {
          id: '2',
          org: '大同支公司',
          premiumIncome: '3706.50',
          premiumPercent: '11.54',
          finalPayRate: '58.60',
          fullPayRate: '59.70',
          marketRate: '21.3',
          operationCostRate: '97.7'
        },
        {
          id: '3',
          org: '阳泉支公司',
          premiumIncome: '2543.25',
          premiumPercent: '7.92',
          finalPayRate: '57.60',
          fullPayRate: '58.89',
          marketRate: '18.5',
          operationCostRate: '97.3'
        },
        {
          id: '4',
          org: '长治支公司',
          premiumIncome: '2583.10',
          premiumPercent: '8.04',
          finalPayRate: '56.86',
          fullPayRate: '57.21',
          marketRate: '17.5',
          operationCostRate: '98.2'
        },
        {
          id: '5',
          org: '晋城支公司',
          premiumIncome: '2523.25',
          premiumPercent: '7.85',
          finalPayRate: '58.60',
          fullPayRate: '59.72',
          marketRate: '21.3',
          operationCostRate: '97.7'
        },
        {
          id: '6',
          org: '朔州支公司',
          premiumIncome: '2510.65',
          premiumPercent: '7.81',
          finalPayRate: '57.60',
          fullPayRate: '58.30',
          marketRate: '18.5',
          operationCostRate: '97.3'
        },
        {
          id: '7',
          org: '晋中支公司',
          premiumIncome: '2603.40',
          premiumPercent: '8.11',
          finalPayRate: '56.86',
          fullPayRate: '57.20',
          marketRate: '17.5',
          operationCostRate: '98.2'
        },
        {
          id: '8',
          org: '运城支公司',
          premiumIncome: '2564.55',
          premiumPercent: '7.98',
          finalPayRate: '58.60',
          fullPayRate: '59.70',
          marketRate: '21.3',
          operationCostRate: '97.7'
        },
        {
          id: '9',
          org: '忻州支公司',
          premiumIncome: '2514.05',
          premiumPercent: '7.83',
          finalPayRate: '57.60',
          fullPayRate: '58.70',
          marketRate: '57.6',
          operationCostRate: '97.3'
        },
        {
