          premiumIncome: '2614.45',
          premiumPercent: '8.14',
          finalPayRate: '56.86',
          fullPayRate: '57.96',
          marketRate: '17.5',
          operationCostRate: '98.2'
        },
        {
          id: '12',
          org: '合计',
          premiumIncome: '32124',
          premiumPercent: '100',
          finalPayRate: '57.67',
          fullPayRate: '58.64',
          marketRate: '19.13',
          operationCostRate: '97.8'
        }
      ]
    }
  },
  methods: {
    inspectTable () {
    },
    exportTable () {
    },
    sum ({ columns, data }) {
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计'
        } else {
          sums[index] = 0
          data.forEach(row => {
            sums[index] += Number(row[column.property])
          })
          if (index >= 3) {
            sums[index] /= data.length
          }
          sums[index] = sums[index].toFixed(2)
        }
      })
      return sums
    }
  }
}
</script>

